import { ethers } from 'ethers';
import MetaMaskWallet from './MetaMaskWallet.js';
import Providers from './Providers.js';

export default class BaseContract {
    constructor () {
        // 务必注意：钱包是实现了Signer接口,所以active_wallet中的一些属性是signer没有的【比如address属性】
        // 所以在后续实例中,只能调用Signer共有的方法,以免出错！【获取地址用getAddress()方法】  || this.active_wallet;
        if (Providers.isMetaMaskProvider()) {
            // 确定是MetaMask钱包
			let provider =  new MetaMaskWallet().currentProvider();
            this.signer = provider.getSigner();
            console.log( 'BaseContract  之 this.signer', this.signer );
        } else {
            // 确定是Ethers自己创建或助记词导入的钱包！
            // this.active_wallet = new EthersWallet().generate_mnemonric_wallet( mnemonric );
            // console.log( 'ethers钱包之active_wallet接口', this.active_wallet );
        }
    }

    createContract ( abi_data, bytecode ) {
        return new ethers.ContractFactory( abi_data, bytecode, this.signer );
    }
 
    createContractInstance ( contract_address, abi_data ) {
        return new ethers.Contract( contract_address, abi_data, this.signer );
    }
}




