import { ethers } from 'ethers';
import MetamaskWallet from './MetaMaskWallet.js';

/*
* 1. Check for injected ethers (mist/metamask)
* 2. If metamask/mist create a new ethers instance and pass on result
* 3. Get networkId - Now we can check the user is connected to the right network to use our dApp
* 4. Get user account from metamask
* 5. Get user balance
*/
// this.signer = new MetamaskWallet().signer;
// console.log( 'this.signer ', this.signer);
let currentProvider  = new MetamaskWallet().currentProvider();
let ethersInstance = ()=> new Promise(function (resolve, reject) {
  // Check for injected ethers (mist/metamask)
	if (typeof currentProvider !== 'undefined') {
		resolve({
		  injectedWeb3: true,
		  ethers () {
			return currentProvider
		  }
		})
	  } else {
		// web3 = new Web3(new Web3.providers.HttpProvider('http://localhost:7545')) GANACHE FALLBACK
		reject(new Error('Unable to connect to Metamask'))
	  }
	})
  .then(result => {
    return new Promise(function (resolve, reject) {
	  currentProvider.getNetwork().then((network)=>{
		  // Assign the networkId property to our result and resolve promise
		  console.log( 'ChainId '  + network.chainId);
		  let networkId = network.chainId;
		  result = Object.assign({}, result, {networkId})
		  resolve(result)
	  }).catch((e) =>{
	      console.log(e);
		  reject(new Error('Unable to retrieve network ID'))
	  });
    })
  })
  .then(result => {
    return new Promise(function (resolve, reject) {
	  // Retrieve coinbase
	  if (window.ethereum) {
		window.ethereum.enable().then((accounts)=>{
			console.log("window.ethereum.enable() " + accounts);  
		}).catch((e) =>{
			  console.log(e);
		});
	  }
	  currentProvider.listAccounts().then((accounts) => {
		console.log("accounts " + accounts[0]);
		let coinbase = accounts[0];
		result = Object.assign({}, result, { coinbase })
		resolve(result)
	  }).catch((e)=>{
		console.log(e);
		reject(new Error('Unable to retrieve coinbase'))
	  });
    })
  })
  .then(result => {
    return new Promise(function (resolve, reject) {
      // Retrieve balance for coinbase
	  currentProvider.getBalance(result.coinbase).then((balance) => {
	      console.log("Balance: " + balance);
		  result = Object.assign({}, result, { balance })
		  resolve(result)
	  }).catch((e)=>{
	      console.log(e);
		  reject(new Error('Unable to retrieve balance for address: ' + result.coinbase))
	  });
    })
  })
export default ethersInstance;



