import BaseContract from './BaseContract.js';

let fairWin_abi = [
		"function investGame(string  inviteCode,string  beInvitedCode) public payable",
		"function withDraw() public",
		"function investAward(uint start ,uint end) public",
		"function shareAward(uint start ,uint end) public",
		"function applySuperNode() public payable",
		"function getGameInfo() public view returns (uint,uint,uint)",
		"function isCodeUsed(string code) public view returns (bool)",
		"function getPlayerInfoPlyId(uint plyId) public view returns (address,uint,uint,uint,uint,uint,uint,uint,uint,uint,uint,bool)",
		"function getPlayerInfoPlyAdd(address plyAdd) public view returns (address,uint,uint,uint,uint,uint,uint,uint,uint,uint,uint,bool)",
		"function getInvestInfoPlyId(uint plyId) public view returns (uint,uint,uint,string memory,string memory)",
		"function getInvestInfoPlyAdd(address plyAdd) public view returns (uint,uint,uint,string memory,string memory)",
		"function balance() public view returns (uint)",
		"function transfer() public",
	];

let contractResolve;
let getContract = new Promise(function (resolve, reject) {
  contractResolve = resolve;
});

export const initContract = () => {
	let fairWin_ContractAddress = "0x0BF82a979d9B5ac6d4270f3BCd6B70265EE0D376";
	let fairWin_contract = new BaseContract().createContractInstance(fairWin_ContractAddress, fairWin_abi);
	if (fairWin_contract == null || typeof fairWin_contract !== 'undefined') {
		  contractResolve(fairWin_contract)
	    console.log( 'create contract success ' );
	}else{
		  console.log( 'create contract fail  ' );
	}
};
export default getContract



