import { ethers } from 'ethers';
import { store } from '../store/'
import MetamaskWallet from './MetaMaskWallet.js';

let pollEthers = function (state) {
  let currentProvider  = new MetamaskWallet().currentProvider();
  setInterval(() => {
    if (currentProvider!=null && (typeof currentProvider !== 'undefined')) {
		return new Promise(function (resolve, reject) {
		  // Retrieve coinbase
		  currentProvider.listAccounts().then((accounts) => {
				let coinbase = accounts[0];
				let result;
				result = Object.assign({}, result, { coinbase })
				resolve(result)
		  }).catch((e)=>{
				console.log(e);
				reject(new Error('Unable to retrieve coinbase'))
		  });
		})
		.then(result => {
		return new Promise(function (resolve, reject) {
		  // Retrieve balance for coinbase
		  currentProvider.getBalance(result.coinbase).then((balance) => {
			  store.dispatch('pollEthers', {
				coinbase: result.coinbase,
				balance: balance 
              })
			  result = Object.assign({}, result, { balance })
			  resolve(result)
		  }).catch((e)=>{
			  //console.log(e);
			  reject(new Error('Unable to retrieve balance for address: ' + result.coinbase))
		  });
		})
	  })
    }
  }, 500)
}

export default pollEthers



