import Vue from 'vue'
import Router from 'vue-router'
import CasinoDapp from '@/components/casino-dapp'
Vue.use(Router)
export default new Router({
    routes: [
        {
            path: '/',
            component: () =>
                import ('@/components/game'),
            hidden: true
        }
    ]
})


