import Vue from 'vue'
import { ethers } from 'ethers';
import Vuex from 'vuex'
import state from './state'
import ethersInstance from '../util/EthersInstance'
import pollEthers from '../util/PollEthers'
import contractInstance, {initContract} from '../util/ContractInstance'
import Providers from '../util/Providers.js';

Vue.use(Vuex)
export const store = new Vuex.Store({
  strict: true,
  state,
  mutations: {
    registerEthersInstance(state, payload) {
      console.log('registerEthersInstance Mutation being executed', payload)
      let result = payload
      state.coinbase = result.coinbase
      state.networkId = result.networkId
      state.balance =  ethers.utils.formatEther(''+result.balance)
      state.isInjected = result.injectedWeb3
      state.web3Instance = result.web3
      pollEthers()
    },
    pollEthersInstance(state, payload) {
      //console.log('pollEthersInstance mutation being executed', payload)
      state.web3.coinbase = payload.coinbase
      state.web3.balance = ethers.utils.formatEther(''+payload.balance) 
    },
    registerContractInstance (state, payload) {
      console.log('Casino contract instance: ', payload)
      state.contractInstance = () => payload
    }
  },
  actions: {
    registerEthers({commit}) {
      console.log('registerEthers Action being executed')
	  if (!Providers.isMetaMaskProvider()) return
      return ethersInstance().then(result => {
        console.log('committing result to registerEthersInstance mutation')
        commit('registerEthersInstance', result)
      }).catch(e => {
        console.log('error in action registerEthers', e)
      })
    },
    pollEthers({commit}, payload) {
      //console.log('pollEthers action being executed')
	  if (!Providers.isMetaMaskProvider()) return;
      commit('pollEthersInstance', payload)
    },
    getContractInstance({commit}) {
		if (!Providers.isMetaMaskProvider()) return;
		initContract();
		return contractInstance.then(result => {
			commit('registerContractInstance', result)
		  }).catch(e => console.log(e))
		}
	}
})



